import PropTypes from 'prop-types';
import { useHistory } from 'react-router-dom';
import styled from 'styled-components';

import DetailsPageTemplate from 'templates/DetailsPageTemplate';
import Header from 'components/atoms/Header/Header';
import Loader from 'components/atoms/Loader';
import Error from 'components/atoms/Error';
import CardBig from 'components/molecules/Card/CardBig';
import CardSmall from 'components/molecules/Card/CardSmall';
// import LastAddedBooks from 'components/molecules/LastAddedBooks';
import Button from 'components/atoms/Button/Button';
import useFetchDetailsData from 'actions/useFetchDetailsData';
import withContext from 'hoc/withContext';
import useFetchBooksData from 'actions/useFetchBooksData';

const StyledGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fill, 200px);
  grid-gap: 2rem;
  margin: 4rem auto;
  justify-items: center;
  justify-content: center;
  position: relative;
`;

const DetailsPage = pageContext => {
  const endpoint = pageContext.location.pathname;

  const { itemData } = useFetchDetailsData(endpoint);
  const history = useHistory();
  // console.log(itemData.data.id);

  const { books } = useFetchBooksData();
  
  
  // console.log(books.data);

  return (
    <>
      <DetailsPageTemplate>
        {itemData.status === 'loading' 
        // || books.status === 'loading' 
        ? (
          <Loader />
        ) : itemData.status === 'error' 
        // || books.status === 'error' 
        ? (
          <Error reloadButton />
        ) : (
          <>
            {itemData &&
              [itemData.data].map((item, index) => (
                <CardBig key={item.id} item={item} index={index} endpoint={endpoint} />
              ))}
            <>
              <Header>Other books by this author</Header>
              {/* <StyledGrid>
                {books && [books].map(book => <CardSmall key={book.id} book={book} id={book.id}  />)}
              </StyledGrid> */}
            </>

            <Button
              onClick={() => {
                history.goBack();
              }}
            >
              Back
            </Button>
          </>
        )}
      </DetailsPageTemplate>
    </>
  );
};

DetailsPage.propTypes = {
  pageContext: PropTypes.oneOf(['home', 'books', 'authors', 'books', 'quotes']),
};

export default withContext(DetailsPage);
