import styled from 'styled-components';

import BookIcon from 'assets/icons/bookIcon.svg';
import Book1 from 'assets/images/book1.jpg';
import ReadingIcon from 'assets/icons/readingIcon.svg';
import Book2 from 'assets/images/book2.jpg';
import BuyIcon from 'assets/icons/buyIcon.svg';
import Book3 from 'assets/images/book3.jpg';

export const Header = styled.h1``;

export const Paragraph = styled.p`
  line-height: 1.5;
  font-size: 1.3rem;
`;

export const BoxFront = styled.div`
  ${Header} {
    font-size: 2.5rem;
    font-weight: 600;
    display: inline-block;
    vertical-align: middle;
    color: white;

    &:before {
      display: block;
      width: 80px;
      height: 80px;
      margin: 0 auto 0.7rem;
      background-size: contain;
      background-position: center;
    }
  }
`;
export const BoxBack = styled.div``;
export const BoxText = styled.div``;

export const Article = styled.div`
  width: 100%;
  height: 450px;
  color: #fff;
  perspective: 1200px;
  position: relative;
  text-align: center;
  display: flex;
  flex-shrink: 1;

  ${BoxFront}, ${BoxBack} {
    transition: transform 1s cubic-bezier(0.4, 0.2, 0.2, 1);
    backface-visibility: hidden;
    overflow: hidden;
    position: relative;
    display: flex;

    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
  }

  ${BoxFront} {
    position: absolute;
    z-index: 2;
    /* width: 100%;
    height: 100%; */
    top: 0;
    left: 0;
    transform: rotateY(0deg);
    background-size: cover;
    background-position: center;
    background-color: hsl(0, 0%, 18%);
    background-blend-mode: overlay;

    ${BoxText} {
      transform: translate3d(0, 0, 0);
      opacity: 1;
    }
  }

  ${BoxBack} {
    transform: rotateY(180deg);
    background: #ae0046;
    position: relative;
    z-index: 1;

    ${BoxText} {
      padding: 0 1.7rem;
      transform: translate3d(80%, 0, 0);
    }
  }

  ${BoxText} {
    transition: transform 0.75s cubic-bezier(0.4, 0.2, 0.2, 1) opacity 0.5s linear;
    opacity: 0;
  }

  &:hover {
    ${BoxFront} {
      transform: rotateY(-180deg);

      ${BoxText} {
        transform: translate3d(-200%, 0, 0);
        opacity: 0;
      }
    }

    ${BoxBack} {
      transform: rotateY(0deg);

      ${BoxText} {
        transform: translate3d(0, 0, 0);
        opacity: 1;
      }
    }
  }

  &:nth-child(1) {
    ${BoxFront} {
      background-image: url(${Book1});

      ${Header}:before {
        background-image: url(${BookIcon});
      }
    }
  }

  &:nth-child(2) {
    ${BoxFront} {
      background-image: url(${Book2});
      ${Header}:before {
        background-image: url(${ReadingIcon});
      }
    }
  }

  &:nth-child(3) {
    ${BoxFront} {
      background-image: url(${Book3});
      ${Header}:before {
        background-image: url(${BuyIcon});
      }
    }
  }
`;
