import { useState } from 'react';

import { StyledWrapper } from './styled';
import styled from 'styled-components';
import { Formik, Form, ErrorMessage } from 'formik';
import api from 'api';
import { validationSchema } from 'components/molecules/Card/QuoteCard/validationSchema.js';
import { initialValues } from 'components/molecules/Card/QuoteCard/initialValues.js';
import RequiredBox from 'components/molecules/RequredBox/RequiredBox';
import ErrorPopup from 'components/molecules/Popups/ErrorPopup';
import SuccessPopup from 'components/molecules/Popups/SuccessPopup';
import { StyledForm, StyledButton, InputField, StyledLabel } from './styled';

import { AiFillHeart, AiOutlineHeart } from 'react-icons/ai';

export const Checkbox = styled.input.attrs({ type: 'checkbox' })`
  width: 3rem;
  height: 3rem;
`;

export const Label = styled.label`
  width: 3rem;
  height: 3rem;
  font-size: 3rem;
  color: ${({ theme }) => theme.colors.lipstick};
`;

const EditQuotePopup = ({isVisible, toggleEditPopup}) => {
  const [isChecked, setChecked] = useState(false);

  const handleChecked = () => {
    setChecked(!isChecked);
  };

  return (
    <StyledWrapper>
        <button onClick={toggleEditPopup}>close</button> 
      <p>To jest popup do edycji cytatów</p>

      <Formik
        initialValues={initialValues}
        validationSchema={validationSchema}
        onSubmit={async values => {
          console.log(values);

          const quote = {
            records: [
              {
                fields: {
                  quote: values.quote,
                  title: values.title,
                  author: values.author,
                  checked: values.checked,
                },
              },
            ],
          };

          api
            .update('/quotes', quote)
            .then(response => {
              // setPopup(true);
              // resetForm();
              // handleClicked();
              console.log(response);
              console.log('Successfully submitted.');
            })
            .catch(err => {
              // setErrorPopup(true);
              console.error(err);
              console.log('The form could not be sent.');
            });
        }}
      >
        {({ values, handleChange, handleBlur, handleSubmit }) => (
          <Form method="POST" autoComplete="off" onSubmit={handleSubmit}>
            {/* <>
              {errorPopup && (
                <ErrorPopup handlePopupClose={handlePopupClose} handleReset={handleReset} />
              )}
              {popup && (
                <SuccessPopup handlePopupClose={handlePopupClose} handleReset={handleReset} />
              )}
            </> */}
            <>
              <StyledLabel as="label" htmlFor="quote">
                quote
              </StyledLabel>
              <InputField
                textarea
                type="text"
                name="quote"
                as="textarea"
                onChange={handleChange}
                onBlur={handleBlur}
                values={values.quote}
              />
              <ErrorMessage name="quote">{msg => <RequiredBox msg={msg} />}</ErrorMessage>
            </>
            <>
              <StyledLabel as="label" htmlFor="title">
                title
              </StyledLabel>
              <InputField
                as="input"
                type="text"
                name="title"
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.title}
                autoComplete="given-name"
              />
              <ErrorMessage name="title">{msg => <RequiredBox msg={msg} />}</ErrorMessage>
            </>
            <>
              <StyledLabel as="label" htmlFor="author">
                author
              </StyledLabel>
              <InputField
                as="input"
                type="text"
                name="author"
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.author}
                autoComplete="given-name"
              />

              <ErrorMessage name="author">{msg => <RequiredBox msg={msg} />}</ErrorMessage>
            </>

            <Label forHtml="checked" checked={isChecked}>
              {isChecked ? <AiFillHeart /> : <AiOutlineHeart />}
            </Label>
            <Checkbox
              type="checkbox"
              name="checked"
              onChange={handleChange}
              onBlur={handleBlur}
              value={values.checked}
              checked={isChecked}
              onClick={handleChecked}
            />
          </Form>
        )}
      </Formik>
      <StyledButton type="submit">
        Update quote
      </StyledButton>
    </StyledWrapper>
  );
};

export default EditQuotePopup;
