import { useState, useEffect } from 'react';
import CardSmall from 'components/molecules/Card/CardSmall';
import { base } from 'airtable/base';
import withContext from 'hoc/withContext';
import useFetchBooksData from 'actions/useFetchBooksData';


const LastAddedBooks = () => {
const [books, setBooks] = useState([]);
// const params = useParams();
// console.log(params);
// const filterByFormula = '{lastName}="Miller"';

// const {books} = useFetchBooksData();

useEffect(() => {

  base('books')
    .select({
      view: 'Grid view',
      pageSize: 5,
      sort:[{ field: 'createdTime', direction: 'desc' }] ,
    })
    .eachPage(
      records => {
        setBooks(records);
        console.log(records);
      },
      function (err) {
        if (err) {
          console.error(err);
          return;
        }
      },
    );
}, []);

return (
  <>
      {books &&
        books.map(book => (
          <CardSmall book={book} key={book.id} />
        ))}
  </>
);

};

export default withContext(LastAddedBooks);
